/**
 *  
 *    key 
 *    value   
 **/
let valueCollection = {}

/**   **/
class ValueItem {
  constructor () {
    this.resolveArr = []
    this.rejectArr = []
    this.value = null
  }
}

/**
 *   '' 
 *   Promise
 *
 *  
 *   ''  emit 
 *   resolve
 *   emit 
 *
 *  @param { String } name -  emit 
 **/
let on = function (name) {
  return new Promise((resolve, reject) => {
    /**
     *   name  resolve 
     *  
     **/
    let target = valueCollection[ name ] =
      valueCollection[ name ] ||
      new ValueItem()
    
    /**
     *  
     **/
    if (target.value) {
      resolve(target.value)
    }
    
    /**
     *  
     *  push resolve  reject
     *   emit 
     **/
    else {
      target.resolveArr.push(resolve)
      target.rejectArr.push(reject)
    }
  })
}

/**
 *  
 *  @param { String } name -  on 
 **/
let emit = function (name, value) {
  /**
   *   name  resolve 
   *  
   **/
  let target = valueCollection[ name ] =
    valueCollection[ name ] ||
    new ValueItem()
  
  /**  **/
  if (target.value) {
    return console.warn(` waitValue ${ name } `)
  }
  
  /**  value  error reject **/
  if (value instanceof Error === true) {
    target.rejectArr.forEach(reject => {
      reject(value)
    })
  }
  
  /**
   *   resolve
   *  
   *  
   **/
  else {
    /**  resolve **/
    target.resolveArr.forEach(resolve => {
      resolve(value)
    })
    
    /** **/
    target.value = value
    
    /**  resolve  reject  **/
    delete target.resolveArr
    delete target.rejectArr
  }
}

let waitValue = {
  on, emit
}

export { waitValue }