const isEmptyValue = function (value) {
  /**
   *  null, undefined, NaN, ''
   *  
   **/
  if (value === null || value === undefined || (typeof value === 'number' && isNaN(value)) || value === '') {
    return true
  }
  
  /**
   *  
   **/
  else if (typeof value === 'string') {
    if (value.replace(/(^\s*)|(\s*$)/g, '') === '') {
      return true
    }
  }
  
  /**  **/
  return false
}

export { isEmptyValue }